@import "../../variabless.scss";

html {
    scroll-behavior: smooth;
}

header {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: $main-color;
    height: 80px;
    color: $text-color;
}

.header-helper {
    padding: 10px;
    display: flex;
    width: 900px;
    max-width: 900px;

    .logo {
        text-align: center;
        border-radius: 3px;
        background-color: #fff;
        color: rgb(60, 60, 60);

        span {
            border-bottom: 2px solid red;
        }
    }

    .links {
        flex-grow: 1;
        display: flex;
        justify-content: flex-end;

        ul {
            padding: 0px;
            display: flex;
            align-items: center;
            margin: 0px;

            li {
                margin: 0px 10px;
                display: inline-block;

                a {
                    text-decoration: none;
                    color: #fff;
                }
            }
        }
    }
}

@media screen and (max-width: 360px) {
    header {

        .header-helper {
            padding: 5px;
        }

        .logo {
            font-size: 15px;
            padding: 5px;
        }

        .links {
            font-size: 15px;
        }
    }
}

@media screen and (min-width: 360px) {
    header {
        .logo {
            padding: 10px;
        }
    }
}
