@import '../../variabless.scss';

body {
    position: relative;
}

footer {
    display: flex;
    justify-content: center;
    background-color: rgb(54, 49, 49);
    height: 200px;
    color: #fff;

    .headline {
        text-align: center;
        font-size: 20px;
    }

    .footer-helper {
        padding: 10px;
        width: 900px;
        max-width: 900px;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    .links {
        font-size: 16px;
        ul {
            list-style: none;
            padding: 0px;

            li {
                display: inline-block;

                a {
                    text-decoration: none;
                    color: #fff;
                    padding: 10px;

                    &:hover {
                        text-decoration: underline;
                    }
                }
            }
        }
    }
}

@keyframes opacity {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

.back-button-wrapper {
    display: flex;
    justify-content: flex-end;
    position: sticky;
    padding: 20px;
    right: 0px;
    bottom: 0px;
    animation: opacity 0.5s;

    .back-button {
        cursor: pointer;
        border-radius: 100%;
        width: 60px;
        height: 60px;
        background-color: $main-color;
        color: black;

        img {
            padding: 20px;
            width: 100%;
        }
    }
}

@media screen and (max-width: 500px) {
    footer {

        .footer-helper {
            justify-content: space-around;
        }

        .links { 
            ul {
                display: flex;
                flex-direction: column;

                li {
                    margin: 10px;
                }
            }
        }
    }
}