.custom-button {
    cursor: pointer;
    color: #fff;
    background-color: rgba(17, 158, 71, 0.787);
    border-radius: 3px;
    border: none;
    padding: 1rem 10%;
    outline: none;

    &:hover {
        background-color: rgba(15, 134, 61, 0.787);
    }

    &:active {
        background-color: rgba(13, 124, 56, 0.787);
    }
}

.fluid {
    width: 100%;
}
